@import "../../global.scss";

.modal_wrapper {
  z-index: 10;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  .modal_bg {
    z-index: -1;
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.7);
    /* filter: blur(5px); */
  }
  .modal_card {
    width: 80%;
    height: 70%;
    margin: 70px auto;
    transform: tra;
    max-height: 95%;
    border: 1px solid rgba(0, 0, 0, 0.1);
    /* overflow: hidden; */
    display: flex;
    flex-flow: column nowrap;
    border-radius: 2px;
    background-color: white;

    position: relative;

    .icon {
      z-index: 2;
      width: 50px;
      height: 50px;
      position: absolute;
      top: 0;
      right: 0;
    }

    .modal_img {
      z-index: 1;
      /* max-width: ; */
      max-height: 250px;
      flex-grow: 1;
      border-radius: 2px 2px 0 0;
    }
    .modal_body {
      padding: 10px;
      position: relative;
      flex-grow: 2;
      h4 {
      }
      p {
        /* white-space: nowrap; */
        overflow: hidden;
        height: inherit;
        font-size: smaller;
        margin-bottom: 1rem;
      }
      label {
        display: block;
      }
      .modal_votes {
        position: absolute;
        bottom: 1em;
        left: 1em;
        width: 37px;
        height: 36px;

        border-radius: 56%;
        background-color: $azul4;
        padding: 0.5rem;
        label {
          height: 100%;
          width: 100%;
          text-align: center;
        }
      }
      .modal_fav {
        height: 10px;
        width: inherit;
        position: absolute;
        bottom: 1.8em;
        right: 0.5em;
        .heart {
          z-index: 4;
          width: 45px;
          height: 45px;
        }
      }
    }
  }
}

// Large devices (desktops, 992px and up)
@media screen and (min-width: 992px) {
  .modal_card {
    width: 60% !important;
    height: 80% !important;
    .modal_body {
      flex-grow: 1.5;
      h4 {
        font-size: xx-large;
      }
      p {        
        font-size:larger !important;
        margin-bottom: 1rem;
      }
    }
  }
}
